@import "variables/fonts";
@import "variables/colors";

@import 'animations';

@import './header';
@import "./footer";
@import "./search";
@import "reviews";
@import "./login";
@import "blog";
@import "single-post";
@import "about";
@import "404";
@import "cookie";

@import "front-page";
@import "page-contact-us";
@import "woocommerce/single-product";
@import "woocommerce/category-product";
@import "woocommerce/archive-product";
@import "woocommerce/cart";
@import "woocommerce/checkout";
@import "woocommerce/dashboard";
@import "woocommerce/wishlist";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    height: 100%;
}

body {
 
}

main {
    min-height: calc(100vh - 70px);
    margin-top: 8rem;
}

footer {
    height: 50px;
}

table {
    border-collapse: collapse;
  }

a, p, li {
    font-family: $archivo;
}

a {
    text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
    font-family: $josefin;
}

textarea:focus, input:focus{
    outline: none;
}

.container-margins {
    margin: 0 4rem;
    @media only screen and (max-width: 800px) {
       margin: 0 1rem;
    }
}

.container-margins-small {
    margin: 0 2rem;
}


$bezier: cubic-bezier(0.64, 0.04, 0.35, 1);
  .to-reveal {
    opacity: 0;
    position: relative;
    top: 20px;
  } 
  
  .revealed {
    transition: 0.6s $bezier;
    opacity: 1;
    position: relative;
    top: 0;
  }


